.container {
  width: 30vw;
  height: 82vh;
  margin: auto;
  margin-top: 10vh;
  border-radius: 12px;
  background-image: linear-gradient(180deg, #130754 0%, #3b2f80 100%);
}

.top-bar {
  display: flex;
  justify-content: center;
  gap: 2.5%;
  padding-top: 7%;
  width: 100%;
  height: 6%;
}

.top-bar input {
  width: 60%;
  height: 100%;
  display: flex;
  background-color: #ebfffc;
  border: none;
  outline: none;
  border-radius: 40px;
  padding-left: 40px;
  color: #626262;
  font-size: 0.8rem; /*12.8px*/
  font-weight: 400;
}

.search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
  height: 100%;
  background-color: #ebfffc;
  border-radius: 50%; /*complete circle*/
  cursor: pointer;
}

.search-icon img {
  width: auto;
  height: 100%;
}

.weather-image {
  margin: 6% 0 3%;
  display: flex;
  justify-content: center;
}

.weather-image img {
  height: 22vh;
}

.weather-temp {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 3rem; /*48px*/
  font-weight: 400;
}

.weather-location {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 2rem; /*32px*/
  font-weight: 400;
}

.weather-description {
  color: white;
  font-size: 1.5rem; /*24px*/
  font-weight: 400;
  display: flex;
  justify-content: center;
}

.data-container {
  margin-top: 10%;
  color: white;
  display: flex;
  justify-content: center;
}

.element {
  margin: auto;
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.data {
  font-size: 1.2rem; /*19.2px*/
  font-weight: 400;
}

.text {
  font-size: 1rem; /*16px*/
  font-weight: 400;
}

.icon {
  margin-top: 0.625rem; /*10px - Margin that are related to the font-size*/
  width: 2vw;
}

/* @media screen and (max-height: 600px) and (max-width: 1290px) {
  .weather-image img {
    height: 18vh;
  }
} */

@media screen and (max-height: 1181px) {
  .weather-image img {
    height: 28vh;
  }
}

@media screen and (max-height: 1080px) {
  .weather-image img {
    height: 20vh;
  }
}

@media screen and (max-height: 750px) {
  .weather-image img {
    height: 22vh;
  }
}

@media screen and (max-width: 820px) {
  .container {
    width: 85vw;
    height: 65vh;
  }
}
